import React, { useEffect, useRef, useState } from 'react';
import { getContentById, getContents, getTemplateById, getTemplates } from './api/Api';
var velocityjs = require("velocityjs");
import './app.css';

const domain = process.env.REACT_APP_STRAPI_API_URL;

function App({ contentName, contentIdsAndTemplateIds }) {
    // TODO: TESTING PURPOSE
    contentName = contentName ? contentName : 'banner';
    contentIdsAndTemplateIds = contentIdsAndTemplateIds ? contentIdsAndTemplateIds : '%5B%7B%22templateId%22:%2250%22,%22contentId%22:5%7D,%7B%22templateId%22:%2248%22,%22contentId%22:3%7D,%7B%22templateId%22:%2248%22,%22contentId%22:2%7D%5D';

    const [htmlCode, setHtmlCode] = useState(null);
    const [CID_TID, set_CID_TID] = useState(decodeURI(contentIdsAndTemplateIds));

    useEffect(async () => {
        const cid_tid_obj = CID_TID.length && JSON.parse(CID_TID);
        // templateId: '48', contentId:
        const getTemplateData = await getTemplates(cid_tid_obj.map(el => el.templateId));
        const getContentsData = await getContents(contentName, cid_tid_obj.map(el => el.contentId));
        mappingOfDataWithTemplate(getTemplateData, getContentsData, cid_tid_obj);
    }, [])

    function mappingOfDataWithTemplate(getTemplateData, getContentsData, cid_tid_obj) {
        if (getTemplateData && getContentsData && Object.keys(cid_tid_obj).length) {
            const finalTemplate = cid_tid_obj.map(contentIdAndTemplateId => {
                const filterContent = getContentsData.filter(content => content.id === contentIdAndTemplateId.contentId);
                const filterTemplate = getTemplateData.filter(template => +template.id === +contentIdAndTemplateId.templateId);
                // To bind domain with image src(url).
                if (filterTemplate.length && filterContent[0] && Object.keys(filterContent[0]).length) {
                    Object.keys(filterContent[0]).map((key, i) => {
                        if (filterContent[0][key] && Array.isArray(filterContent[0][key])) {
                            // For MultiMedia
                            if (filterContent[0][key] && filterContent[0][key][0] && filterContent[0][key][0]['ext']) {
                                filterContent[0][key].map(el => {
                                    return el.url = domain + el.url;
                                });
                            }
                        } else if (filterContent[0][key] && typeof filterContent[0][key] === 'object') {
                            // For SingleMedia
                            if (filterContent[0][key] && filterContent[0][key]['ext']) {
                                if (typeof filterContent[0][key] === 'object') {
                                    filterContent[0][key].url = domain + filterContent[0][key].url;
                                }
                            }

                            // object
                            console.log('filterContent[0][key]', filterContent[0][key]);
                        }
                    });
                    return velocityjs.render(filterTemplate[0].contentShape, { content: filterContent[0] });
                }
                return "";
            });
            setHtmlCode(finalTemplate);
        }
    }

    return (
        <>
            {
                htmlCode ?
                    <div dangerouslySetInnerHTML={{
                        __html: htmlCode
                    }}>
                    </div> : <h1>Loading...</h1>
            }
        </>
    );

}

export default App;
